import { InjectionToken } from '@angular/core';

export const ACTIVITY_MONITOR_CONFIG = new InjectionToken<Partial<ActivityMonitorConfig>>('activity-monitor-config');

export interface ActivityMonitorConfig {
  /**
   * Duration of user inactivity that ends the countdown.
   * Default value is 180 seconds.
   */
  maxInactivityDuration: number;
  /**
   * Duration of countdown.
   * Default value is 30 seconds.
   */
  countdownDuration: number;
}

export type ActivityEvent = { type: 'start' | 'end' | 'reset' } | { type: 'tick'; remaining: number };
